
import {
  IconHeart,
  IconMessage,
  IconStar,
  IconStarFill,
  IconHeartFill,
} from '@arco-design/web-vue/es/icon';
import { Comment, Avatar } from "@arco-design/web-vue"
import { defineComponent } from 'vue';

export default defineComponent({
  components: {
    IconHeart,
    IconMessage,
    IconStar,
    IconStarFill,
    IconHeartFill,
    Comment,
    Avatar
  },
  data: () => {
    return {
      like: false,
      star: false,
    };
  },
  methods: {
    onStarChange() {
      this.star = !this.star;
    },
    onLikeChange() {
      this.like = !this.like;
    },
  },
});
